// @flow
import * as React from "react"
import LayoutContent from "./LayoutContent";

type Props = {
    name?: String,
    children: React.Node
}

export const MAIN = "main";

class LayoutSlot extends React.Component<Props> {

    static defaultProps = {
        name: "main"
    };

    render()
    {
        const { name, children }  = this.props;


        const kids = React.Children.toArray(React.Children.only(children).props.children);

        let nonLayoutContent = [];

        for (let i = 0; i < kids.length; i++)
        {
            const kid = kids[i];
            if (kid.type === LayoutContent)
            {
                if (kid.props.name === name)
                {
                    return kid;
                }
            }
            else
            {
                nonLayoutContent.push(kid);
            }
        }

        return name === MAIN && <React.Fragment>
            { nonLayoutContent }
        </React.Fragment>;
    }
}

export default LayoutSlot
